import { useFormik } from "formik";

const Shade = () => {
  return (
    <div style={{ ...styles.shade }}>
      
    </div>
  )
}

const AddTask = (props) => {
  const form = useFormik({
    initialValues: {
      text: ''
    },
    onSubmit: (v) => {
      if (v.text.trim() === '') {
        return;
      }
      props.onAddTask(v.text);
      props.onCloseForm();
      form.values.text = '';
    }
  });

  const handleCloseForm = (e) => {
    e.preventDefault();
    props.onCloseForm();
    form.values.text = '';
  }

  return (
    <div className={!props.formVisible ? 'hide' : ''}>
      <form
        className="white row center-align"
        style={{ ...styles.wrapper }}
        onSubmit={form.handleSubmit}
      >

        <h4 className="center-align">
          Add Task
        </h4>

        <div className="input-field col s10 offset-s1">
          <input
            id="text"
            type="text"
            className="validate"
            value={form.values.text}
            onChange={form.handleChange}
          />
          <label htmlFor="text">Text</label>
        </div>

        <button
          type="submit"
          value="Add"
          className="col s4 offset-s1 waves-effect waves-light btn green"
        >
          Add
        </button>

        <button
          className="col s4 offset-s1 waves-effect waves-light btn red"
          onClick={handleCloseForm}
        >
          Close
        </button>

      </form>
      <Shade />
    </div> 
  )
}

const styles = {
  wrapper: {
    padding: '0 0 20px',
    position: 'fixed',
    width: '350px',
    maxWidth: '100%',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    zIndex: '999'
  },
  shade: {
    position: 'fixed',
    backgroundColor: '#000000cf',
    top: '0',
    left: '0',
    bottom: '0',
    right: '0',
    zIndex: '998'
  }
};

export default AddTask
