const Progress = ({ overall, completed }) => {
  let width = `${(100 / overall) * completed}%`;

  if (overall === 0) {
    width = '100%';
  }

  return (
    <>
      <div className="progress" style={{ ...styles.progress }}>
        <div
          className="determinate"
          style={{ ...styles.inner, width }}
        >
        </div>
      </div>
    </>
  )
}

const styles = {
  progress: {
    height: '20px',
    borderRadius: '20px',
    marginTop: '20px'
  },
  inner: {
    display: 'flex',
    alignItem: 'center',
    justifyContent: 'center'
  } 
}

export default Progress
