const Task = (props) => {
  const { index, text, done  } = props;

  const doneEl = done ? (
    <span
      className="material-icons green-text tooltipped"
      onClick={() => props.onToggleDone(index)}
      style={{ ...styles.doneCloseIcon }}
      data-position="top"
      data-tooltip="Completed!"
    >
      done
    </span>
  ) : (
    <span
      className="material-icons red-text tooltipped"
      onClick={() => props.onToggleDone(index)}
      style={{ ...styles.doneCloseIcon }}
      data-position="top"
      data-tooltip="Not completed!"
    >
      close
    </span>
  );

  const textEl = (
    <span style={done ? {...styles.taskdone, ...styles.taskText} : {...styles.taskText}}>
      {text}
    </span>
  )

  return (
    <li className="collection-item valign-wrapper" style={{ ...styles.task }}>
      
      {doneEl} <span style={{ ...styles.indent }}>{'    '}</span>
      {textEl}

      <button
        style={{ ...styles.deleteBtn }}
        className="waves-effect waves-red btn-flat red-text"
        onClick={props.onDeleteTask}
      >
        delete
      </button>

    </li>
  )
}

const styles = {
  task: {
    marginTop: '2px',
    position: 'relative'
  },
  taskText: {
    position: 'relative',
    zIndex: '299',
    overflow: 'auto'
  },
  taskdone: {
    textDecoration: 'line-through'
  },
  indent: {
    whiteSpace: 'pre'
  },
  doneCloseIcon: {
    cursor: 'pointer'
  },
  deleteBtn: {
    position: 'absolute',
    top: '50%',
    right: '0',
    transform: 'translateY(-50%)',
    zIndex: '300',
    backgroundColor: 'white'
  }
};


export default Task
