import { useEffect, useState } from "react";
import { getTasksFromStorage, makeTaskBordered, setTasksToStorage } from "utils";
import AddTask from "./AddTask";
import Progress from "./Progress";
import SearchTask from "./SearchTask";
import Task from "./Task";

const Tasks = () => {
  const [choose, setChoose] = useState('all');
  const [formVisible, setFormVisible] = useState(false);
  const [tasks, setTasks] = useState(getTasksFromStorage());
  const [searchValue, setSearchValue] = useState('');

  useEffect(() => makeTaskBordered());
  useEffect(() => setTasksToStorage(tasks), [tasks]);
  useEffect(() => makeTaskBordered(), [choose]);

  const handleChangeSearchValue = (e) => {
    setSearchValue(e.target.value);
  };

  const handleChangeChoose = (to) => {
    setChoose(to);
  };

  const handleAddTask = (text) => {
    setTasks([
      ...tasks,
      { text, done: false }
    ]);
  };

  const handleToggleDone = (index) => {
    const copy = [...tasks];
    copy[index].done = !copy[index].done;
    setTasks(copy);
  };

  const hanleDeleteTask = (index) => {
    const copy = [...tasks];
    copy.splice(index, 1);
    setTasks(copy);
  };

  const filteredTasks = tasks.filter(task => {
    if (task.text.includes(searchValue.trim())) {
      return true
    }
    return false;
  });

  const renderedTasks = filteredTasks.map(task => {
    const renderedTask = ( 
      <Task
        key={tasks.indexOf(task)}
        index={tasks.indexOf(task)}
        text={task.text}
        done={task.done}
        onToggleDone={handleToggleDone}
        onDeleteTask={hanleDeleteTask}
      />
    );
    switch (choose) {
      case 'all':
        return renderedTask;
      case 'done':
        if (task.done) {
          return renderedTask
        }
        break;
      case 'undone':
        if (!task.done) {
          return renderedTask
        }
        break;
    }
  });

  return (
    <ul
      className="z-depth-4 collection with-header grey lighten-2 col s12 m8 offset-m2 l6 offset-l3"
      style={{ ...styles.collection }}
    >

      <li className="collection-header grey lighten-2">

        <h3 style={{ ...styles.title }}>
          Tasks

          <a
            className="btn-floating btn-large waves-effect waves-light red"
            style={{ ...styles.addBtn }}
            onClick={() => setFormVisible(true)}
          >
            <i className="material-icons">add</i>
          </a>

        </h3>

      </li>

      <ul className="tabs tabs-transparent" style={{ ...styles.tabs }}>
        <li className="tab">
          <a
            onClick={() => handleChangeChoose('all')}
            className="waves-effect waves-yellow black-text"
          >
            All
          </a>
        </li>
        <li className="tab">
          <a
            onClick={() => handleChangeChoose('done')}
            className="waves-effect waves-yellow black-text"
          >
            Done
          </a>
        </li>
        <li className="tab">
          <a
            onClick={() => handleChangeChoose('undone')} 
            className="waves-effect waves-yellow black-text"
          >
            Undone
          </a>
        </li>
      </ul>

      <SearchTask
        searchValue={searchValue}
        onChangeSearchValue={handleChangeSearchValue}
      />

      {renderedTasks}

      <AddTask
        onAddTask={handleAddTask}
        onCloseForm={() => setFormVisible(false)}
        formVisible={formVisible}
      />

      <Progress
        overall={tasks.length}
        completed={tasks.filter((t) => t.done).length}
      />

    </ul>
  )
}

const styles = {
  collection: {
    overflow: 'visible',
    paddingBottom: '0px',
    borderRadius: '15px'
  },
  title: {
    position: 'relative'
  },
  addBtn: {
    position: 'absolute',
    top: '50%',
    right: '0',
    transform: 'translateY(-50%)'
  },
  tabs: {
    marginBottom: '10px'
  }
};

export default Tasks
