import * as $ from 'jquery';

const ls = localStorage;

const setTasksToStorage = (tasks) => {
  ls.setItem('tasks', JSON.stringify(tasks));
}

const getTasksFromStorage = () => {
  const tasks = ls.getItem('tasks');

  if (tasks === 'undefined' || tasks === null) {
    ls.setItem('tasks', '[]');
    return [];
  } else {
    return JSON.parse(tasks);
  }
};

const addTaskToStorage = (text, done) => {
  const task = { text, done };
  const tasks = JSON.parse(ls.getItem('tasks'));
  tasks.push(task);
  setTasksToStorage(tasks);
};

function makeTaskBordered() {
  $('.collection-item').css('border-radius', '0');

  $('.collection-item').eq(0).css('border-top-left-radius', '10px');
  $('.collection-item').eq(0).css('border-top-right-radius', '10px');

  $('.collection-item').eq(-1).css('border-bottom-left-radius', '10px');
  $('.collection-item').eq(-1).css('border-bottom-right-radius', '10px');
}

export {
  setTasksToStorage,
  getTasksFromStorage,
  addTaskToStorage,
  makeTaskBordered
};
